import { Input, Form, Button, Flex, Rate } from "antd";
import { useTodos } from "../store";

const UpdateForm = ({openUpdate, setOpenUpdate, todo}) => {
    const updateData = useTodos(state=>state.updateData)
    const onFinish = (values) => {
        updateData(values.title, values.body, values.rate, todo[0].id)
        setOpenUpdate(!openUpdate);
    }
    
    const initialValues = {
        title: todo[0].title,
        body: todo[0].body,
        rate: todo[0].rate
    }
    return (
        <Form
        name="form-update"
        onFinish={onFinish}
        initialValues={initialValues}
    >
        <Form.Item 
            name="title"
            label=""
            
            rules={[
                {
                    required: true,
                    message: '  '
                }
            ]}
            
        >
            <Input value={todo[0].title} />
        </Form.Item>
        <Form.Item 
            name="body"
            label=""
            rules={[
                {
                    required: true,
                    message: '  '
                }
            ]}
        >
            <Input.TextArea/>
        </Form.Item>
        <Form.Item name="rate" label=""
            rules={[
                {
                    required: true,
                    message: '  '
                }
            ]}
        >
            <Rate/>
        </Form.Item>
        <Form.Item>
            <Flex gap="small">
                <Button type="primary" htmlType="submit">
                    
                </Button>
                <Button type="default" htmlType="reset">
                     
                </Button>
            </Flex>

        </Form.Item>
    </Form>
    )
}

export default UpdateForm